
"use client";
import { useAuth } from "@/contexts/AuthContext";
import DashboardCard from "@/app/components/DashboardCard";
import Hero from "@/app/components/Hero";
import ServicesSection from "@/app/components/ServicesSection"; // Importamos la nueva sección

export default function HomePage() {
    const { user, loading } = useAuth();

    if (loading) {
        // Mantenemos un loader simple mientras se verifica la autenticación
        return (
            <div className="flex justify-center items-center h-screen">
                <p className="text-xl text-gray-700">Cargando...</p>
            </div>
        );
    }

    // Si el usuario NO está autenticado, mostramos la página de aterrizaje
    if (!user) {
        return (
            <div>
                <Hero />
                <ServicesSection />
                {/* Aquí podríamos agregar más secciones en el futuro, como testimonios o un "Sobre nosotros" */}
            </div>
        );
    }

    // Si el usuario SÍ está autenticado, mostramos su panel de control
    if (user) {
        return (
            <main className="flex-grow p-4 sm:p-6 lg:p-8 bg-gray-50">
                <div className="max-w-7xl mx-auto">
                    <div className="text-left mb-8">
                        <h1 className="text-3xl font-bold text-gray-900">¡Bienvenido de nuevo!</h1>
                        <p className="text-gray-600 mt-1">Aquí tienes un resumen de todo lo que puedes hacer.</p>
                    </div>
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
                        <DashboardCard title="Pedir Turno de Consulta" link="/turnos/consulta" icon="/file.svg" />
                        <DashboardCard title="Pedir Turno de Peluquería" link="/turnos/peluqueria" icon="/window.svg" />
                        <DashboardCard title="Ver Mis Turnos" link="/turnos" icon="/globe.svg" />
                        <DashboardCard title="Administrar Mis Mascotas" link="/mascotas" icon="/file.svg" />
                        <DashboardCard title="Ir a la Tienda" link="/tienda" icon="/window.svg" />
                        <DashboardCard title="Ver Mascotas en Adopción" link="/adopciones" icon="/globe.svg" />
                    </div>
                </div>
            </main>
        );
    }
}
